.introHeader {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 5rem;
    width: 100%;
    height: 90vh;
    margin-bottom: 10rem;
}
@media only screen and (max-width: 1100px) {
    .introHeader {
        height: 75vh;
    }
  }
@media only screen and (max-width: 900px) {
    .introHeader {
        height: 60vh;
        padding-bottom: 10rem;
    }
  }

  @media only screen and (max-width: 600px) {
    .introHeader {
        height: 80vh;
        padding-bottom: 0;
    }
  }





.imageContainerHeader {
    width: 80%;
    height: 40%;
    position: relative;
    border-radius: 2rem;
}

@media only screen and (max-width: 1100px) {
    .imageContainerHeader {
        height: 30%;
    }
  }
  
@media only screen and (max-width: 600px) {
    .imageContainerHeader {
        height: 20%;
        width: 100%;
    }
  }

.introHeader h4 {
    width: 45%;
    font-size: 2.8rem;
    font-family: 'Playfair Display', sans-serif;
    line-height: 1.2;
    letter-spacing: .8px;
    font-weight: 400;
    text-align: center;
    padding: 0;
}
@media only screen and (max-width: 1100px) {
    .introHeader h4 {
        width: 60%;

    }
  }
@media only screen and (max-width: 900px) {
    .introHeader h4 {
        width: 70%;
        font-size: 2.4rem;
    }
  }
  @media only screen and (max-width: 600px) {
    .introHeader h4 {
        width: 70%;
        font-size: 2rem;
    }
  }

.imageContainerHeader img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 2rem;
    opacity: .6;

}

.introHeader p {
    width: 80%;
    font-size: 1.6rem;
    font-family: 'lora', sans-serif;
    line-height: 1.2;
    font-weight:lighter;
    text-align: center;
    opacity: .8;
    margin: 0;
}

.coverContainer {
    width: 100%;
    height: 40vh;
    position: relative;
    margin: 10rem auto;
}
@media only screen and (max-width: 1100px) {
    .coverContainer {
        height: 35vh;
    }
  }
  @media only screen and (max-width: 900px) {
    .coverContainer {
        height: 25vh;
    }
  }

  @media only screen and (max-width: 600px) {
    .coverContainer {
        height: 20vh;
    }
  }

.coverContainer img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: 55% 55%;
    border-radius: 2rem;
}

.coverContainer h2 {
    position: absolute;
    top: 40%;
    transform: translateY(-40%);
    left: 50%;
    transform: translateX(-50%);
    font-family: 'Lora', serif;
    font-size: 3.2rem;
    text-align: center;
    text-transform: capitalize;
    font-weight: 400;
    letter-spacing: 1.2px;
    color: var(--color-neon-blue);
    text-shadow: 2px 2px 2px var(--color-black);
    transition: .5s all ease-in-out;
}
@media only screen and (max-width: 600px) {
    .coverContainer h2 {
        top: 30%;
    }
  }

.coverContainer p {
    position: absolute;
    top: 40%;
    transform: translateY(-40%);
    left: 50%;
    transform: translateX(-50%);
    font-family: 'Lora', serif;
    font-size: 1.8rem;
    opacity: 0;
    transition: .5s all ease-in-out;
    text-align: center;
    /* text-shadow: 1px 1px 1px var(--color-black); */

}


.analogSectionCenterLink {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 55%;
    text-decoration: none;
    color: var(--color-primary-white);
    text-shadow: 1px 1px 1px var(--color-black);
    font-size: 1.6rem;
    padding: 2px 12px;
    border: 2px solid var(--color-grey-blue);
    border-radius: 1.2rem;
    display: inline-block;

transition: background-color 0.1s ease-in-out, 
            border 0.1s ease-in-out,
            top 0.5s ease-in-out,
            opacity 0.5s ease-in-out;

    opacity: 0;
}

.analogSectionCenterLink:hover {
    color: rgba(0, 0, 0, .8);
    background-color: rgba(255, 255, 255, .60);
}

.coverContainer:hover h2 {
    top: 35%;
    opacity: 0;
}

.coverContainer:hover p {
    opacity: .8;
    top: 42%;
}
@media only screen and (max-width: 600px) {
    .coverContainer:hover p {
        top: 18%;
    }
  }
.coverContainer:hover .analogSectionCenterLink {
    opacity: 1;
    top: 60%;
}
@media only screen and (max-width: 600px) {
    .coverContainer:hover .analogSectionCenterLink   {
        top: 70%;
    }
  }
