.introHeader {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 3rem;
    width: 100%;
    height: 90vh;
    margin-bottom: 10rem;
}
@media only screen and (max-width: 1100px) {
    .introHeader {
        margin-bottom: 0;
        height: 60vh;
    }
  }

  @media only screen and (max-width: 900px) {
    .introHeader {
        height: 80vh;
    }
  }

.imageContainerHeader {
    width: 80%;
    height: 40%;
    position: relative;
    border-radius: 2rem;
}

@media only screen and (max-width: 1100px) {
    .imageContainerHeader {
        height: 30%;
    }
  }
  @media only screen and (max-width: 600px) {
    .imageContainerHeader {
        height: 20%;
        width: 100%;
    }
  }

.introHeader h4 {
    width: 45%;
    font-size: 3.2rem;
    font-family: 'Playfair Display', sans-serif;
    line-height: 1.2;
    letter-spacing: .8px;
    font-weight: 700;
    text-align: center;
    margin: 0;
    color: var(--color-primary-white);
    text-shadow: 1px 1px 1px var(--color-neon-blue);

}

.imageContainerHeader img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 2rem;
    opacity: .6;

}

.introHeader p {
    width: 50%;
    font-size: 1.8rem;
    font-family: 'lora', sans-serif;
    line-height: 2.2;
    font-weight:lighter;
    text-align: center;
    opacity: .8;
    margin: 0;
    padding: 4rem;
    height: 50%;
}
@media only screen and (max-width: 600px) {
    .introHeader p {
        line-height: 1.6;
        width: 80%;
        padding: 2rem;
    }
  }

.introHeader a {
    text-decoration: none;
    color: var(--color-neon-blue);
}

@media only screen and (max-width: 1100px) {
    .introHeader p {
        height: auto;
    }
  }

  @media only screen and (max-width: 900px) {
    .introHeader p {
        width: 80%;
    }
  }

.gallery {
    margin: auto;
    grid-column: full-start / full-end;
    width: 80%;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(24, 5vw);
    grid-gap: 1.5rem;
    padding: 0 1.5rem;

}
@media only screen and (max-width: 1100px) {
    .gallery {
        width: 100%;
    }
  }
  @media only screen and (max-width: 900px) {
    .gallery {
        grid-gap: .5rem;
    }
  }
  @media only screen and (max-width: 600px) {
    .gallery  {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
  }
.galleryItem {
    position: relative;
    transition: .15s all ease-in;
}
@media only screen and (max-width: 600px) {
    .galleryItem  {
        height: 40vh;
        width: 100%;
  }
}

.galleryItem:hover {
    cursor: pointer;
    transform: translateY(-2px);
    opacity: .9;
}

.galleryItem img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
    position: absolute;
    border-radius: 5px;
}
.galleryItem0 {
    grid-row: 1 / span 6;
    grid-column: 1 / span 6;
}
.galleryItem1 {
    grid-row: 1 / span 4;
    grid-column: 6 / span 7;
}
.galleryItem2 {
    grid-row: 5 / span 4;
    grid-column: 6 / span 7;
}
.galleryItem3 {
    grid-row: 7 / span 6;
    grid-column: 1 / span 6;
}
.galleryItem4 {
    grid-row: 9 / span 8;
    grid-column: 6 / span 7;
}
.galleryItem5 {
    grid-row: 13 / span 6;
    grid-column: 1 / span 6;
}
.galleryItem6 {
    grid-row: 17 / span 7;
    grid-column: 6 / span 7;
}
.galleryItem7 {
    grid-row: 19 / span 5;
    grid-column: 1 / span 6;
}
